<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-row>
        <el-col>
          <el-button type="primary"></el-button>
        </el-col>
      </el-row>
      <el-table :data="rolesList" border>
        <!--  -->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row
              :class="['bor_bottom', i1 === 0 ? 'bor_top' : '', 'vcenter']"
              v-for="(item1, i1) in scope.row.children"
              :key="item1.id"
            >
              <!--  -->
              <el-col :span="5">
                <el-tag closable @close="removeRightsById(scope.row, item1.id)">{{item1.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!--  -->
              <el-col :span="19">
                <el-row
                  v-for="(item2, i2) in item1.children"
                  :key="item2.id"
                  :class="['vcenter', i2 === 0 ? '' : 'bor_top']"
                >
                  <el-col :span="6">
                    <el-tag
                      type="success"
                      closable
                      @close="removeRightsById(scope.row, item2.id)"
                    >{{item2.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <el-tag
                      v-for="item3 in item2.children"
                      :key="item3.id"
                      type="warning"
                      closable
                      @close="removeRightsById(scope.row, item3.id)"
                    >{{item3.authName}}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="roleName"></el-table-column>
        <el-table-column label="" prop="roleDesc"></el-table-column>
        <el-table-column label="" width="320px">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>
            <el-button type="danger" icon="el-icon-delete" size="mini"></el-button>
            <el-button type="warning" icon="el-icon-setting" size="mini" @click="editRrightsDialog(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" @close="resetRights" width="50%">
      <el-tree :data="rightsTree" :props="treeProps" show-checkbox default-expand-all
      node-key="id" :default-checked-keys="defKeys" ref="treeRef">
      </el-tree>
      <span slot="footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editRights"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      rolesList: [],
      editDialogVisible: false,
      // 
      rightsTree: [],
      treeProps: {
        label: 'authName', // 
        children: 'children' // 
      },
      // id
      defKeys: [],
      roleId: ''
    }
  },

  created () {
    this.getRolesList()
  },

  methods: {

    // 
    async getRolesList () {
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) { return this.$message.error('') }
      this.rolesList = res.data
    },

    // id
    async removeRightsById (role, id) {
      // 
      const confirmInfo = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      if (confirmInfo !== 'confirm') return this.$message.info('')
      // 
      const { data: res } = await this.$http.delete(
        `roles/${role.id}/rights/${id}`
      )
      if (res.meta.status !== 200) return this.$message.error('')
      role.children = res.data // scop.rowscope.row.children
    },

    // 
    async editRrightsDialog (role) {
      this.roleId = role.id
      const { data: res } = await this.$http.get('rights/tree')
      if (res.meta.status !== 200) return this.$message.error('')
      this.rightsTree = res.data
      this.getDefKeys(role, this.defKeys)
      console.log(this.defKeys)
      // 
      this.editDialogVisible = true
    },
    // id
    getDefKeys (node, arr) {
      // children
      if (!node.children) {
        return arr.push(node.id) // id
      }
      // 
      node.children.forEach(item => this.getDefKeys(item, arr))
    },
    // 
    resetRights () {
      this.defKeys = []
    },
    // 
    async editRights () {
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(), // ...key,key
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      const idStr = keys.join(',')

      const { data: res } = await this.$http.post(`roles/${this.roleId}/rights`, { rids: idStr })
      if (res.meta.status !== 200) return this.$message.error('')

      this.$message.success('')
      this.getRolesList()
      this.editDialogVisible = false
    }
  }
}
</script>

<style lang="less" scoped>
.el-tag {
  margin: 7px;
}
.vcenter {
  display: flex;
  align-items: center;
}
.bor_top {
  border-top: 1px solid #eee;
}
.bor_bottom {
  border-bottom: 1px solid #eee;
}
</style>
